<script>
import $ from 'jquery';

export default {
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    animate_slide_fadein(elementString) {
      const element = document.querySelector(`.${elementString}`);
      $(element).toggleClass('animate-slide-hidden');
    },
    toggle_category() {
      const folded = document.querySelector('.header__hamburger__folded');
      const cross = document.querySelector('.header__hamburger__cross');
      const category = document.querySelector('.category');
      $(folded).toggleClass('d-none');
      $(cross).toggleClass('d-none');
      $(category).toggleClass('d-none');
      setTimeout(() => {
        this.animate_slide_fadein('category__list');
        this.toggle_arrow('category');
      }, 1);
    },
    toggle_arrow(elementString) {
      const leftArrow = document.querySelector(`.${elementString}__leftArrow`);
      const rightArrow = document.querySelector(`.${elementString}__rightArrow`);
      const list = document.querySelector(`.${elementString}__list`);
      const listLeft = $(list).scrollLeft();
      const listRight = listLeft + $(list).width();
      const items = document.querySelectorAll(`.${elementString}__list__item`);
      let itemsWidthSum = 0;
      items.forEach((item) => {
        itemsWidthSum += $(item).outerWidth(true);
      });
      itemsWidthSum = Math.floor(itemsWidthSum);
      if (listLeft <= 0) {
        $(leftArrow).fadeOut();
      } else {
        $(leftArrow).fadeIn();
      }
      if (listRight >= itemsWidthSum) {
        $(rightArrow).fadeOut();
      } else {
        $(rightArrow).fadeIn();
      }
    },
    animate_scroll(elementString, directionNum) {
      const element = document.querySelector(`.${elementString}__list`);
      if (directionNum > 0) {
        // 向右移動
        $(element).animate({
          scrollLeft: '+=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      } else if (directionNum < 0) {
        // 向左移動
        $(element).animate({
          scrollLeft: '-=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      }
    },
    bind_scroll_event(elementString) {
      $(window).bind('mousewheel', (e) => {
        // deltaY 正往右，負往左
        if (e.originalEvent.deltaY > 0) {
          this.animate_scroll(elementString, 1);
        } else if (e.originalEvent.deltaY < 0) {
          this.animate_scroll(elementString, -1);
        }
      });
    },
    unbind_scroll_event() {
      $(window).unbind('mousewheel');
    },
    go_top() {
      $('html').animate({ scrollTop: '0' }, 500);
    },
    move_borderbottom() {
      // create an array with obj like
      // {
      //   path: '',
      //   left: '',
      //   width: '',
      // }
      const tempArray = [];
      const pathArray = this.$router.options.routes[2].children;
      const liArray = document.querySelectorAll('.header__nav__list__item');
      pathArray.forEach((element, index) => {
        tempArray[index] = {};
        tempArray[index].path = element.path;
        tempArray[index].left = liArray[index].offsetLeft;
        tempArray[index].width = $(liArray[index]).width();
      });
      // get current path
      const currentPath = this.$router.history.current.path;
      // find which path matches current path
      const pathIndex = tempArray.findIndex((element) => `/front/${element.path}` === currentPath);
      // get borderbottom initial left
      const headerNavWidth = $('.header__nav').width();
      const headerNavListWidth = $('.header__nav__list').width();
      const borderBottomInitialLeft = headerNavWidth - headerNavListWidth - 15;
      // rendor
      $('.header__nav__borderbottom').css({
        left: `${borderBottomInitialLeft + tempArray[pathIndex].left}px`,
        width: `${tempArray[pathIndex].width}px`,
      });
    },
  },
  mounted() {
    const header = document.querySelector('.header');
    const headerHamburgerFolded = document.querySelector('.header__hamburger__folded');
    const headerHamburgerCross = document.querySelector('.header__hamburger__cross');
    const category = document.querySelector('.category');
    const categoryList = document.querySelector('.category__list');
    let windowWidth = $(window).width();

    $(headerHamburgerFolded).addClass('d-none');
    $(headerHamburgerCross).removeClass('d-none');
    $(category).removeClass('d-none');
    $(categoryList).removeClass('animate-slide-hidden');

    this.toggle_arrow('header__nav');
    this.toggle_arrow('category');

    const headerHeight = $(header).height();
    const categoryTop = category.offsetTop;
    $(window).bind('scroll', () => {
      const windowScrollTop = $(window).scrollTop();
      if (windowScrollTop + headerHeight >= categoryTop) {
        $(category).addClass('category--fixed');
      } else {
        $(category).removeClass('category--fixed');
      }
    });

    $(window).resize(() => {
      windowWidth = $(window).width();
      if (windowWidth >= 1024) {
        $(category).removeClass('d-none');
        $(categoryList).removeClass('animate-slide-hidden');
        this.move_borderbottom();
      } else if (windowWidth < 1024) {
        this.toggle_arrow('header__nav');
        this.toggle_arrow('category');
      }
    });

    this.move_borderbottom();
  },
};
</script>
